html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Lexend", sans-serif;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.section {
  padding: 2rem 0;
}

h1 {
  margin: 0;
  line-height: 1.5;
}

h2 {
  margin: 0;
  line-height: 1.5;
}

h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  margin: 1rem 0;
  font-size: 2rem;
  color: #111;
}

a {
  text-decoration: none;
  line-height: 1.6;
  font-size: 2rem;
}

[class$=__title] {
  margin: 2rem 0;
}

.header {
  background-image: url(../assets/img/header.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0 20rem;
}
.header__top-bar {
  display: none;
}
@media (min-width: 768px) {
  .header__top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__search {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__search {
    margin-top: 30rem;
  }
}
.header__search-p {
  color: #ffffff;
}
.header__search-btn {
  display: block;
  padding: 1rem 2rem;
  background-color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  color: #1ccad8;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .header__search-btn {
    display: inline-block;
  }
}
@media (hover: hover) {
  .header__search-btn:hover {
    background-color: #e6e6e6;
    cursor: pointer;
  }
}

.logo-text {
  font-size: 2.4rem;
  font-weight: 700;
  color: #1ccad8;
}

.destinations__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.destination {
  display: flex;
  justify-content: center;
  align-items: center;
}
.destination__picture {
  width: 14rem;
}
.destination__name {
  color: #1ccad8;
  margin: 0;
}

.places__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.places__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .places__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .places__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.place__img {
  border-radius: 3rem;
}
.place__name {
  text-align: center;
}

.hosts {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 5rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #111 0%, #111 50%, transparent 50%), url(../assets/img/anfitrion.jpg);
  background-size: 100%, 65rem;
  background-position: center, bottom;
  background-repeat: no-repeat;
  color: #ffffff;
}
@media (min-width: 480px) {
  .hosts {
    background-position: center, center 150%;
  }
}
@media (min-width: 768px) {
  .hosts {
    padding: 15rem 3rem;
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #111 0%, #111 48%, transparent 50%), url(../assets/img/anfitrion.jpg);
    background-size: 100%, auto;
    background-position: center, 80% 30%;
  }
}
@media (min-width: 1024px) {
  .hosts {
    background-position: center, right center;
  }
}
@media (min-width: 768px) {
  .hosts__content {
    max-width: 50%;
  }
}
.hosts__title {
  margin: 1rem 0;
}
.hosts__text {
  color: #ffffff;
  margin: 0 0 3rem;
}
.hosts__btn {
  display: block;
  padding: 1rem 2rem;
  background-color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  color: #111;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .hosts__btn {
    display: inline-block;
  }
}
@media (hover: hover) {
  .hosts__btn:hover {
    background-color: #e6e6e6;
    cursor: pointer;
  }
}

.discover-experiences__grid {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.discover-experiences__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .discover-experiences__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.experience__img {
  border-radius: 3rem;
}
.experience__name {
  text-align: center;
}

.footer {
  padding-top: 5rem;
  background-color: #f3f3f3;
  border-top: 0.2rem solid #cecece;
}
.footer__grid {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__titulo {
  text-transform: uppercase;
  font-size: 2rem;
}
.footer__bottom-bar {
  margin-top: 3rem;
  border-top: 0.2rem solid white;
  padding: 2rem 0;
}

.nav {
  display: flex;
  gap: 2rem;
}
.nav--footer, .nav--bottom-bar {
  margin-top: 1rem;
  flex-direction: column;
  gap: 1rem;
}
.nav--bottom-bar {
  margin: 0;
}
@media (min-width: 768px) {
  .nav--bottom-bar {
    flex-direction: row;
  }
}
.nav__link {
  color: #ffffff;
  padding: 1rem;
  border-radius: 3rem;
  transition: all 0.2s ease-in-out;
}
@media (hover: hover) {
  .nav__link:hover {
    background-color: #f0f0f0;
    color: #686868;
  }
}
.nav__link--footer, .nav__link--bottom-bar {
  color: #686868;
  padding: 0;
  border-radius: 0;
}
@media (hover: hover) {
  .nav__link--footer:hover, .nav__link--bottom-bar:hover {
    background-color: inherit;
    color: #111;
  }
}
.nav__link--bottom-bar::before {
  content: "-";
  margin-right: 0.5rem;
}

.form {
  margin: 5rem auto 2rem;
  padding: 3rem 0;
  background-color: #ffffff;
  border-radius: 3rem;
  box-shadow: 0px 3px 7px 0px #686868;
}
@media (min-width: 1024px) {
  .form {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.form__camp {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  transition: all 0.2s ease-in-out;
  border-radius: 3rem;
}
@media (min-width: 1024px) {
  .form__camp:nth-child(1) {
    padding-left: 2rem;
  }
}
@media (hover: hover) {
  .form__camp:hover {
    background-color: #f0f0f0;
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
  }
}
.form__camp-label {
  margin: 0 1rem 0.5rem;
  font-size: 1.4rem;
  font-weight: 900;
}
.form__camp-input {
  width: 100%;
  color: #686868;
  border: none;
  outline: none;
}
.form__camp-btn {
  display: block;
  height: 4rem;
  padding: 0 3rem;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  background-image: radial-gradient(circle, #d32163 0%, rgb(189, 30, 89) 100%);
  border: none;
  border-radius: 3rem;
}